// pages/admin/index.tsx
import { useSession } from 'next-auth/react';
import { useRouter } from 'next/router';
import Link from 'next/link';
import { useEffect, useMemo, useState } from 'react';

type StateCode =
  | 'AL' | 'AK' | 'AZ' | 'AR' | 'CA' | 'CO' | 'CT' | 'DE' | 'FL' | 'GA'
  | 'HI' | 'ID' | 'IL' | 'IN' | 'IA' | 'KS' | 'KY' | 'LA' | 'ME' | 'MD'
  | 'MA' | 'MI' | 'MN' | 'MS' | 'MO' | 'MT' | 'NE' | 'NV' | 'NH' | 'NJ'
  | 'NM' | 'NY' | 'NC' | 'ND' | 'OH' | 'OK' | 'OR' | 'PA' | 'RI' | 'SC'
  | 'SD' | 'TN' | 'TX' | 'UT' | 'VT' | 'VA' | 'WA' | 'WV' | 'WI' | 'WY';

type Region = 'West' | 'Midwest' | 'South' | 'Northeast';

type StateMeta = {
  code: StateCode;
  name: string;
  region: Region;
  x: number; // % across container
  y: number; // % down container
};

// Rough US-shaped constellation layout: not geographic-precise, but feels right.
const STATES: StateMeta[] = [
  // West coast & Rockies
  { code: 'WA', name: 'Washington', region: 'West', x: 8, y: 16 },
  { code: 'OR', name: 'Oregon', region: 'West', x: 12, y: 26 },
  { code: 'CA', name: 'California', region: 'West', x: 10, y: 43 },
  { code: 'ID', name: 'Idaho', region: 'West', x: 17, y: 23 },
  { code: 'NV', name: 'Nevada', region: 'West', x: 18, y: 39 },
  { code: 'AZ', name: 'Arizona', region: 'West', x: 22, y: 53 },
  { code: 'UT', name: 'Utah', region: 'West', x: 24, y: 38 },
  { code: 'MT', name: 'Montana', region: 'West', x: 23, y: 14 },
  { code: 'WY', name: 'Wyoming', region: 'West', x: 27, y: 26 },
  { code: 'CO', name: 'Colorado', region: 'West', x: 30, y: 40 },
  { code: 'NM', name: 'New Mexico', region: 'West', x: 31, y: 55 },
  { code: 'AK', name: 'Alaska', region: 'West', x: 6, y: 82 },
  { code: 'HI', name: 'Hawaii', region: 'West', x: 17, y: 80 },

  // Midwest & Plains
  { code: 'ND', name: 'North Dakota', region: 'Midwest', x: 38, y: 18 },
  { code: 'SD', name: 'South Dakota', region: 'Midwest', x: 38, y: 30 },
  { code: 'NE', name: 'Nebraska', region: 'Midwest', x: 40, y: 42 },
  { code: 'KS', name: 'Kansas', region: 'Midwest', x: 42, y: 53 },
  { code: 'MN', name: 'Minnesota', region: 'Midwest', x: 46, y: 20 },
  { code: 'IA', name: 'Iowa', region: 'Midwest', x: 47, y: 32 },
  { code: 'MO', name: 'Missouri', region: 'Midwest', x: 48, y: 46 },
  { code: 'WI', name: 'Wisconsin', region: 'Midwest', x: 52, y: 23 },
  { code: 'IL', name: 'Illinois', region: 'Midwest', x: 52, y: 36 },
  { code: 'MI', name: 'Michigan', region: 'Midwest', x: 57, y: 25 },
  { code: 'IN', name: 'Indiana', region: 'Midwest', x: 56, y: 38 },
  { code: 'OH', name: 'Ohio', region: 'Midwest', x: 60, y: 36 },

  // South
  { code: 'TX', name: 'Texas', region: 'South', x: 44, y: 70 },
  { code: 'OK', name: 'Oklahoma', region: 'South', x: 40, y: 64 },
  { code: 'AR', name: 'Arkansas', region: 'South', x: 48, y: 60 },
  { code: 'LA', name: 'Louisiana', region: 'South', x: 50, y: 72 },
  { code: 'MS', name: 'Mississippi', region: 'South', x: 54, y: 68 },
  { code: 'AL', name: 'Alabama', region: 'South', x: 58, y: 68 },
  { code: 'TN', name: 'Tennessee', region: 'South', x: 56, y: 58 },
  { code: 'KY', name: 'Kentucky', region: 'South', x: 57, y: 50 },
  { code: 'GA', name: 'Georgia', region: 'South', x: 62, y: 66 },
  { code: 'FL', name: 'Florida', region: 'South', x: 68, y: 77 },
  { code: 'SC', name: 'South Carolina', region: 'South', x: 66, y: 62 },
  { code: 'NC', name: 'North Carolina', region: 'South', x: 67, y: 55 },
  { code: 'VA', name: 'Virginia', region: 'South', x: 68, y: 48 },
  { code: 'WV', name: 'West Virginia', region: 'South', x: 64, y: 44 },
  { code: 'MD', name: 'Maryland', region: 'South', x: 71, y: 45 },

  // Northeast
  { code: 'DE', name: 'Delaware', region: 'Northeast', x: 73, y: 50 },
  { code: 'NJ', name: 'New Jersey', region: 'Northeast', x: 75, y: 44 },
  { code: 'PA', name: 'Pennsylvania', region: 'Northeast', x: 71, y: 39 },
  { code: 'NY', name: 'New York', region: 'Northeast', x: 74, y: 32 },
  { code: 'CT', name: 'Connecticut', region: 'Northeast', x: 78, y: 38 },
  { code: 'RI', name: 'Rhode Island', region: 'Northeast', x: 80, y: 36 },
  { code: 'MA', name: 'Massachusetts', region: 'Northeast', x: 79, y: 31 },
  { code: 'VT', name: 'Vermont', region: 'Northeast', x: 77, y: 26 },
  { code: 'NH', name: 'New Hampshire', region: 'Northeast', x: 80, y: 25 },
  { code: 'ME', name: 'Maine', region: 'Northeast', x: 83, y: 18 },
];

type BatchSummary = {
  id: number;
  batchCode: string;
  productName?: string | null;
  primaryCategory?: string | null;
  isActive?: boolean | null;
  brand?: { id: number; name: string } | null;
  labResults?: {
    id: number;
    passed?: boolean | null;
    lab?: { id: number; name: string } | null;
  }[];
  updatedAt?: string | null;
  // stateCode may or may not be present depending on API implementation
  stateCode?: string | null;
};

type StateStats = {
  totalBatches: number;
  activeBatches: number;
  labs: number;
  brands: number;
  lastUpdated: string | null;
};

type GlobalStats = StateStats & {
  totalStatesWithData: number;
};

const STATE_HUES: Partial<Record<StateCode, number>> = {
  CA: 160,
  CO: 190,
  OR: 155,
  WA: 180,
  NV: 200,
  ME: 150,
  MA: 210,
  NY: 190,
  FL: 170,
  TX: 200,
};

function stateColor(code: StateCode) {
  const base = STATE_HUES[code] ?? 190;
  // We’re not using base directly in classes (Tailwind), but it gives variation if you want inline styles later
  return {
    haloFrom: `rgba(94,234,212,0.32)`,
    haloTo: `rgba(56,189,248,0.28)`,
    pillFrom: 'from-emerald-200/90',
    pillTo: 'to-sky-200/90',
    pillIdle:
      'bg-slate-900/70 border-slate-300/40 text-slate-900/80 dark:bg-slate-900/80 dark:border-slate-500/50 dark:text-slate-100',
  };
}

export default function AdminStateExplorer() {
  const { data: session, status } = useSession();
  const router = useRouter();

  const [selectedState, setSelectedState] = useState<StateCode>('ME');
  const [stateStats, setStateStats] = useState<Partial<Record<StateCode, StateStats>>>({});
  const [stateLoading, setStateLoading] = useState(false);
  const [stateError, setStateError] = useState<string | null>(null);

  const [globalStats, setGlobalStats] = useState<GlobalStats | null>(null);
  const [globalError, setGlobalError] = useState<string | null>(null);
  const [globalLoading, setGlobalLoading] = useState(false);

  useEffect(() => {
    if (status === 'authenticated') {
      void loadGlobalStats();
      void loadStateStats('ME');
    }
  }, [status]);

  async function loadGlobalStats() {
    try {
      setGlobalLoading(true);
      setGlobalError(null);

      const res = await fetch('/api/admin/batches');
      if (!res.ok) {
        const txt = await res.text();
        throw new Error(txt || 'Failed to load global stats');
      }
      const data: BatchSummary[] = await res.json();

      if (!data || data.length === 0) {
        setGlobalStats({
          totalBatches: 0,
          activeBatches: 0,
          labs: 0,
          brands: 0,
          lastUpdated: null,
          totalStatesWithData: 0,
        });
        return;
      }

      const brands = new Set<number>();
      const labs = new Set<number>();
      const statesWithData = new Set<string>();
      let activeCount = 0;
      let latest: string | null = null;

      for (const b of data) {
        if (b.isActive !== false) activeCount++;
        if (b.brand?.id) brands.add(b.brand.id);
        if (b.labResults) {
          for (const lr of b.labResults) {
            if (lr.lab?.id) labs.add(lr.lab.id);
          }
        }
        if (b.stateCode) statesWithData.add(b.stateCode);
        const updated = b.updatedAt;
        if (updated && (!latest || new Date(updated) > new Date(latest))) {
          latest = updated;
        }
      }

      setGlobalStats({
        totalBatches: data.length,
        activeBatches: activeCount,
        labs: labs.size,
        brands: brands.size,
        lastUpdated: latest,
        totalStatesWithData: statesWithData.size,
      });
    } catch (err: any) {
      console.error('Failed to load global stats', err);
      setGlobalError(err.message || 'Failed to load global stats');
    } finally {
      setGlobalLoading(false);
    }
  }

  async function loadStateStats(code: StateCode) {
    try {
      setStateLoading(true);
      setStateError(null);

      const res = await fetch(`/api/admin/batches?stateCode=${encodeURIComponent(code)}`);
      if (res.status === 401) {
        throw new Error('Unauthorized');
      }
      if (!res.ok) {
        const txt = await res.text();
        throw new Error(txt || 'Failed to load batches');
      }

      const data: BatchSummary[] = await res.json();

      if (!data || data.length === 0) {
        setStateStats(prev => ({
          ...prev,
          [code]: {
            totalBatches: 0,
            activeBatches: 0,
            labs: 0,
            brands: 0,
            lastUpdated: null,
          },
        }));
        return;
      }

      const brands = new Set<number>();
      const labs = new Set<number>();
      let activeCount = 0;
      let latest: string | null = null;

      for (const b of data) {
        if (b.isActive !== false) activeCount++;
        if (b.brand?.id) brands.add(b.brand.id);
        if (b.labResults) {
          for (const lr of b.labResults) {
            if (lr.lab?.id) labs.add(lr.lab.id);
          }
        }
        const updated = b.updatedAt;
        if (updated && (!latest || new Date(updated) > new Date(latest))) {
          latest = updated;
        }
      }

      setStateStats(prev => ({
        ...prev,
        [code]: {
          totalBatches: data.length,
          activeBatches: activeCount,
          labs: labs.size,
          brands: brands.size,
          lastUpdated: latest,
        },
      }));
    } catch (err: any) {
      console.error('Failed to load state stats', err);
      setStateError(err.message || 'Failed to load state batches');
    } finally {
      setStateLoading(false);
    }
  }

  const selectedMeta = useMemo(
    () => STATES.find(s => s.code === selectedState),
    [selectedState]
  );
  const selectedStats = selectedState ? stateStats[selectedState] : undefined;

  function handleStateClick(code: StateCode) {
    setSelectedState(code);
    if (!stateStats[code]) {
      void loadStateStats(code);
    }
  }

  if (status === 'loading') {
    return (
      <div className="min-h-screen bg-gradient-to-b from-slate-950 via-slate-900 to-slate-950 text-slate-50 flex items-center justify-center">
        <div className="rounded-2xl border border-slate-800 bg-slate-900/70 px-6 py-4 text-sm text-slate-300">
          Loading admin…
        </div>
      </div>
    );
  }

  if (!session) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-slate-950 via-slate-900 to-slate-950 text-slate-50 flex items-center justify-center">
        <div className="rounded-2xl border border-slate-800 bg-slate-900/70 px-6 py-4 text-sm text-slate-300">
          Please sign in as an admin to view the CartFax state explorer.
        </div>
      </div>
    );
  }

  const globalLastUpdated = globalStats?.lastUpdated
    ? new Date(globalStats.lastUpdated).toLocaleString()
    : null;

  const stateLastUpdated = selectedStats?.lastUpdated
    ? new Date(selectedStats.lastUpdated).toLocaleString()
    : null;

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-950 via-slate-900 to-slate-950 text-slate-50">
      <main className="px-4 py-10 md:px-8 max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex flex-col gap-4 md:flex-row md:items-end md:justify-between">
          <div>
            <div className="inline-flex items-center gap-2 rounded-full border border-emerald-400/40 bg-emerald-300/10 px-3 py-1 text-[11px] uppercase tracking-[0.18em] text-emerald-200 shadow-[0_0_40px_rgba(52,211,153,0.5)]">
              <span className="h-1.5 w-1.5 rounded-full bg-emerald-400 shadow-[0_0_16px_rgba(52,211,153,0.9)]" />
              CartFax · State explorer
            </div>
            <h1 className="mt-3 text-2xl md:text-3xl font-semibold text-slate-50">
              A living constellation of legal cannabis batches.
            </h1>
            <p className="mt-2 max-w-xl text-sm text-slate-300">
              Each state is a glowing node in the CartFax network. Tap one to see how many
              batches, brands, and labs we&apos;re tracking on the ground.
            </p>
          </div>

          {/* Quick admin shortcuts */}
          <div className="flex gap-2 md:gap-3 flex-wrap md:justify-end">
            <Link
              href="/admin/uploads"
              className="rounded-full border border-slate-400/60 bg-slate-100/5 px-3 py-1.5 text-xs text-slate-100 hover:border-emerald-300/80 hover:text-emerald-100 transition"
            >
              COA uploads
            </Link>
            <Link
              href="/admin/batches"
              className="rounded-full border border-slate-400/60 bg-slate-100/5 px-3 py-1.5 text-xs text-slate-100 hover:border-emerald-300/80 hover:text-emerald-100 transition"
            >
              Batch list
            </Link>
            <button
              onClick={() => router.push(`/admin/states/${selectedState}`)}
              className="rounded-full bg-emerald-300 text-slate-950 px-3 py-1.5 text-xs font-medium hover:bg-emerald-200 transition shadow-[0_0_35px_rgba(52,211,153,0.8)]"
            >
              Deep dive {selectedState} →
            </button>
          </div>
        </div>

        {/* Big glass card */}
        <section className="relative overflow-hidden rounded-3xl border border-slate-300/10 bg-white/5 px-4 py-6 md:px-6 md:py-8 backdrop-blur-3xl shadow-[0_40px_140px_rgba(15,23,42,0.9)]">
          {/* Ambient gradients */}
          <div
            className="pointer-events-none absolute -top-32 -left-10 h-72 w-72 rounded-full opacity-60 blur-3xl"
            style={{
              background:
                'radial-gradient(circle at 20% 30%, rgba(94,234,212,0.45), transparent 60%)',
            }}
          />
          <div
            className="pointer-events-none absolute -bottom-40 right-0 h-80 w-80 rounded-full opacity-60 blur-3xl"
            style={{
              background:
                'radial-gradient(circle at 70% 70%, rgba(56,189,248,0.4), transparent 60%)',
            }}
          />

          <div className="relative flex flex-col gap-8 md:grid md:grid-cols-[minmax(0,3fr)_minmax(0,2.1fr)] md:gap-10">
            {/* Abstract US constellation */}
            <div className="relative">
              <div className="flex items-center justify-between gap-3">
                <div className="text-[11px] uppercase tracking-[0.22em] text-slate-300">
                  Interactive state field
                </div>
                <div className="flex items-center gap-3 text-[11px] text-slate-300">
                  <span className="inline-flex items-center gap-1">
                    <span className="h-2 w-2 rounded-full bg-emerald-300 shadow-[0_0_8px_rgba(52,211,153,0.9)]" />
                    Has batches
                  </span>
                  <span className="inline-flex items-center gap-1">
                    <span className="h-2 w-2 rounded-full bg-slate-500/80" />
                    No data yet
                  </span>
                </div>
              </div>

              <div className="mt-4 relative h-[420px] md:h-[460px] rounded-3xl border border-slate-200/20 bg-slate-950/40 backdrop-blur-3xl overflow-hidden">
                {/* Soft “continent” haze */}
                <div
                  className="pointer-events-none absolute inset-0 opacity-60"
                  style={{
                    background:
                      'radial-gradient(circle at 45% 45%, rgba(148,163,184,0.28), transparent 60%), radial-gradient(circle at 75% 30%, rgba(209,250,229,0.4), transparent 55%)',
                  }}
                />

                {STATES.map(s => {
                  const isSelected = s.code === selectedState;
                  const stats = stateStats[s.code];
                  const hasData = !!stats && stats.totalBatches > 0;
                  const colors = stateColor(s.code);

                  return (
                    <button
                      key={s.code}
                      onClick={() => handleStateClick(s.code)}
                      style={{
                        position: 'absolute',
                        left: `${s.x}%`,
                        top: `${s.y}%`,
                        transform: 'translate(-50%, -50%)',
                      }}
                      className={[
                        'group relative flex flex-col items-center justify-center rounded-full px-3 py-2 text-[10px] font-medium transition-all',
                        'backdrop-blur-xl border shadow-[0_18px_45px_rgba(15,23,42,0.85)]',
                        isSelected
                          ? `border-emerald-200/90 bg-gradient-to-br ${colors.pillFrom} ${colors.pillTo} text-slate-950 ring-2 ring-emerald-300/60 scale-[1.06]`
                          : hasData
                            ? 'bg-slate-100/90 text-slate-900 border-emerald-200/60 hover:border-emerald-300/90 hover:shadow-[0_0_35px_rgba(52,211,153,0.8)]'
                            : 'bg-slate-900/80 text-slate-300 border-slate-500/50 hover:border-slate-200/80 hover:text-slate-50',
                      ].join(' ')}
                    >
                      {/* halo */}
                      {isSelected && (
                        <span
                          className="pointer-events-none absolute -inset-2 rounded-full opacity-70 blur-2xl"
                          style={{
                            background: `radial-gradient(circle at 50% 50%, ${colors.haloFrom}, transparent 60%)`,
                          }}
                        />
                      )}

                      <span className="relative z-10 flex items-baseline gap-1">
                        <span className="text-[11px] font-semibold tracking-wide">
                          {s.code}
                        </span>
                        <span className="text-[9px] opacity-80">{s.name}</span>
                      </span>

                      <span className="relative z-10 mt-0.5 text-[9px] text-slate-700 group-hover:text-slate-800">
                        {hasData
                          ? `${stats?.totalBatches ?? 0} batch${
                              (stats?.totalBatches ?? 0) === 1 ? '' : 'es'
                            }`
                          : 'No data yet'}
                      </span>

                      {/* tiny status dot */}
                      <span
                        className={[
                          'pointer-events-none absolute -bottom-1 -right-0.5 h-2 w-2 rounded-full',
                          hasData ? 'bg-emerald-400 shadow-[0_0_10px_rgba(52,211,153,0.9)]' : 'bg-slate-500',
                        ].join(' ')}
                      />
                    </button>
                  );
                })}
              </div>
            </div>

            {/* Detail card */}
            <div className="relative">
              <div className="md:absolute md:right-0 md:top-0 md:w-full">
                <div className="rounded-2xl border border-white/15 bg-slate-900/80 px-4 py-4 backdrop-blur-3xl shadow-[0_24px_90px_rgba(15,23,42,0.95)]">
                  <div className="flex items-start justify-between gap-3">
                    <div>
                      <div className="text-[11px] uppercase tracking-[0.18em] text-slate-400">
                        State snapshot
                      </div>
                      <div className="mt-2 flex items-baseline gap-2">
                        <div className="text-xl font-semibold text-slate-50">
                          {selectedState}
                        </div>
                        <div className="text-sm text-slate-200">
                          {selectedMeta?.name ?? 'Unknown'}
                        </div>
                      </div>
                      <div className="mt-1 text-[11px] text-slate-400">
                        {selectedMeta?.region} · CartFax coverage in real time
                      </div>
                    </div>
                    <button
                      onClick={() => router.push(`/admin/states/${selectedState}`)}
                      className="hidden md:inline-flex items-center gap-1 rounded-full border border-slate-700/80 bg-slate-900/80 px-3 py-1.5 text-[11px] text-slate-200 hover:border-emerald-300/80 hover:text-emerald-100 transition"
                    >
                      Open full view
                      <span aria-hidden>↗</span>
                    </button>
                  </div>

                  {/* Stats grid */}
                  <div className="mt-4 grid grid-cols-2 gap-3 text-xs">
                    <div className="rounded-xl border border-slate-800 bg-slate-950/70 px-3 py-2">
                      <div className="text-[10px] uppercase tracking-[0.16em] text-slate-400">
                        Total batches
                      </div>
                      <div className="mt-1 text-lg font-semibold text-slate-50">
                        {selectedStats ? selectedStats.totalBatches : '—'}
                      </div>
                    </div>
                    <div className="rounded-xl border border-emerald-400/50 bg-emerald-400/10 px-3 py-2">
                      <div className="text-[10px] uppercase tracking-[0.16em] text-emerald-200">
                        Active batches
                      </div>
                      <div className="mt-1 text-lg font-semibold text-emerald-100">
                        {selectedStats ? selectedStats.activeBatches : '—'}
                      </div>
                    </div>
                    <div className="rounded-xl border border-slate-800 bg-slate-950/70 px-3 py-2">
                      <div className="text-[10px] uppercase tracking-[0.16em] text-slate-400">
                        Unique labs
                      </div>
                      <div className="mt-1 text-lg font-semibold text-slate-50">
                        {selectedStats ? selectedStats.labs : '—'}
                      </div>
                    </div>
                    <div className="rounded-xl border border-slate-800 bg-slate-950/70 px-3 py-2">
                      <div className="text-[10px] uppercase tracking-[0.16em] text-slate-400">
                        Brands in state
                      </div>
                      <div className="mt-1 text-lg font-semibold text-slate-50">
                        {selectedStats ? selectedStats.brands : '—'}
                      </div>
                    </div>
                  </div>

                  {/* Activity / errors */}
                  <div className="mt-4 flex flex-col gap-2 text-[11px] text-slate-400">
                    {stateLoading && (
                      <div>Syncing {selectedState}… crunching batches and labs.</div>
                    )}
                    {stateError && (
                      <div className="text-red-400">
                        {stateError === 'Unauthorized'
                          ? 'Your session is missing admin privileges for this endpoint.'
                          : stateError}
                      </div>
                    )}
                    {selectedStats && stateLastUpdated && (
                      <div>Last batch activity: {stateLastUpdated}</div>
                    )}
                  </div>

                  {/* Global footer row */}
                  <div className="mt-4 border-t border-slate-800/80 pt-3 text-[11px] text-slate-400 flex flex-wrap items-center gap-2">
                    {globalLoading && <span>Loading nationwide totals…</span>}
                    {!globalLoading && globalStats && (
                      <>
                        <span className="inline-flex items-center gap-1">
                          <span className="h-1.5 w-1.5 rounded-full bg-emerald-300" />
                          {globalStats.totalBatches} batches tracked
                        </span>
                        <span className="inline-flex items-center gap-1">
                          <span className="h-1.5 w-1.5 rounded-full bg-sky-300" />
                          {globalStats.labs} labs · {globalStats.brands} brands
                        </span>
                        <span className="inline-flex items-center gap-1">
                          <span className="h-1.5 w-1.5 rounded-full bg-slate-400" />
                          {globalStats.totalStatesWithData} states with data
                        </span>
                        {globalLastUpdated && (
                          <span className="inline-flex items-center gap-1">
                            <span className="h-1.5 w-1.5 rounded-full bg-slate-500" />
                            System updated {globalLastUpdated}
                          </span>
                        )}
                      </>
                    )}
                    {globalError && (
                      <span className="text-red-400">
                        Global stats error: {globalError}
                      </span>
                    )}
                  </div>

                  {/* Mobile deep-dive button */}
                  <button
                    onClick={() => router.push(`/admin/states/${selectedState}`)}
                    className="mt-3 inline-flex w-full items-center justify-center rounded-full border border-slate-700/80 bg-slate-900/80 px-3 py-1.5 text-[11px] text-slate-200 hover:border-emerald-300/80 hover:text-emerald-100 transition md:hidden"
                  >
                    Open full view for {selectedState} ↗
                  </button>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}
